 body {
     margin: 0 !important;
     padding: 0 !important;
     ;
 }

 body:before {
     content: '' !important;
     ;
     position: fixed !important;
     ;
     width: 100vw !important;
     ;
     height: 100vh !important;
     ;
     background-image: url("../../assets/css/water-kid.jpg") !important;
     ;
     background-position: center center !important;
     ;
     background-repeat: no-repeat !important;
     ;
     background-attachment: fixed !important;
     ;
     background-size: cover !important;
     ;
     -webkit-filter: blur(10px) !important;
     ;
     -moz-filter: blur(10px) !important;
     ;
     -o-filter: blur(10px) !important;
     ;
     -ms-filter: blur(10px) !important;
     ;
     filter: blur(10px) !important;
     ;
 }

 .contact-form {
     position: absolute;
     top: 50%;
     left: 50%;
     -webkit-transform: translate(-50%, -50%);
         -ms-transform: translate(-50%, -50%);
             transform: translate(-50%, -50%);
     width: 400px;
     height: 500px;
     padding: 80px 40px;
     -webkit-box-sizing: border-box;
             box-sizing: border-box;
     background: rgba(0, 0, 0, .3);
 }

 .uttrakhand {
     display: block;

     width: 80px;
     height: 80px;
     border-radius: 50%;
     overflow: hidden;

     margin: auto;


 }

 .contact-form h2 {
     margin: 0;
     padding: 0 0 20px;
     color: #fff;
     text-align: center;
     text-transform: uppercase;
 }

 .contact-form label {
     z-index: 0;
    top: 36px;
    position: relative;
     margin: 0;
     padding: 0;
     font-weight: bold;
     color: #fff;
    -webkit-transition: 300ms ease all;
    -o-transition: 300ms ease all;
    transition: 300ms ease all;
 }
.shrink{
  top:3px !important;
}
 .contact-form input {
     width: 100%;
     margin-bottom: 20px;
 }

 .contact-form input[type="text"] {
     z-index:1000000;
     border: none;
     border-bottom: 1px solid #fff;
     background: transparent;
     outline: none;
     height: 40px;
     color: #fff;
     font-size: 16px;
 }

 .contact-form input[type="submit"] {
     height: 30px;
     color: #fff;
     font-size: 15px;
     background: red;
     cursor: pointer;
     border-radius: 25px;
     border: none;
     outline: none;
     margin-top: 15%;
 }

 .contact-form a {
     color:#F8D568;
     font-size: 14px;
     font-weight: bold;
     text-decoration: none;
 }
 


